/* Clickable scroll buttons (Prev + Next) */
.cd-vertical-nav {
    list-style-type: none;
    /* lateral navigation */
    position: fixed;
    z-index: 1;
    right: 3%;
    top: 80%;
    bottom: auto;
    transform: translateY(-50%);
    display: none;
}

.cd-vertical-nav a {
    display: block;
    height: 40px;
    width: 40px;
    /* image replace */
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    opacity: 0.5;
    background: url(../icons/cd-icon-arrow.svg) no-repeat center center;
}

.cd-vertical-nav a.cd-prev {
    transform: rotate(180deg);
    margin-bottom: 2vh;
}

.cd-vertical-nav a.inactive {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.2s 0s, visibility 0s 0.2s;
}

@media only screen and (min-width: 1050px) {
    .cd-vertical-nav {
        display: block;
    }
}

